export class PicBookPages {
    public title: string;
    public text: string;
    public pictureLoc: string;
     

    constructor(myTitle, myText, myPictureLoc){
        this.title = myTitle;
        this.text = myText;
        this.pictureLoc = myPictureLoc;
    }
}


